*{
    margin: 0px;
    padding: 0;
}

html{
    scroll-behavior: smooth;

}

.navbar{
    display: flex;
   align-items: center;
   /* justify-content: center; */
   position: -webkit-sticky;
   position: sticky;
   top: 0;
  border-bottom: 2px solid orangered;
  

}
.nav-items{
width: 70%;
display: flex;
font-size: 21px;
justify-content: center;
text-shadow: gray 1px 1px 1px 1px;
}
.nav-items li{
list-style: none;
padding: 3%;
}

.nav-items li a{
text-decoration: none;
color: orangered;

}

.nav-items li a:hover{
    color: white;
    font-size: 25px;
}

























.logo{
    width: 20%;
    display: flex;
    justify-content: center;
    align-items: center;
    transition: all 0.5s ease;

}

.logo:hover{
    width: 25%;
    /* transition: 1s; */
}

.logo img{
    width: 100%;
    border: tomato;
   
}


.right-nav{
    width: 30%;
    text-align: right;
    padding: 0px 23px;
    vertical-align: middle;
}
#search{
    padding: 5px;
    font-size: 17;
    border: 2px solid orangered;
    border-radius: 13px;
}






/* nav.navbar.background{
background-blend-mode: overlay;
} */

.background{
    background: rgba( 0, 0, 0, 0.7) url(/hireme_react_portfolio/static/media/bg.999bb25a.jpg);
    background-size: cover;
    background-blend-mode: darken ;
    background-attachment: fixed;
    background-position: center;
}
.boxmain{
display: flex;
 justify-content: center; 
 align-items: center; 
 font-family: 'Times New Roman', Times, serif; 
/* max-width: 50%; */
margin: auto;
margin-left: 10px;
height: 100%;
padding: 0 133px;
}
.firstsection{
    height: 100vh;
   
}

.imghalf{
width: 30%;
border: 20 px white;
padding: 20px;
    
}
.imghalf img{
    width: 100%;
    border: 4px solid orangered;
    padding: 4px;
    border-radius: 75px;
    display: block;
    margin: auto;
}
.imghalf img:hover{
    border: 4px solid white;
}


.texthalf{
    width: 70%;
}
.text-big{
    color: white;
    font-size: 80px;
    text-shadow: 1px 1px #000;
}
.text-small{
    color: rgb(230, 127, 10);
    font-size: 30px;
    font-family: Tourney;
    display: contents; 
    width: 500px;
    
}


.buttons{
text-decoration: none;
}



.btn{
    padding:  12px 30px;
    margin: 7px 3px;
    border: 2px solid orangered;
    border-radius: 8px ;
    background: none;
    color: orangered;
    font-family: 'Gill Sans', 'Gill Sans MT', Calibri, 'Trebuchet MS', sans-serif;
    font-weight: 900;
    cursor: pointer;
}
.btn-sml{
    padding:  5px 15px;
    
}
.hire-btn{
background:  orangered;
color : white;
}
.btn:hover{
    border: 2px solid white;
    color: white;
}





/*first section ends here */









.secondSection{
    display: flex;
    align-items: center;
    justify-content: center;
    
    
}
.para{
    margin: 72px 50px ;
  
}

.thumbnail{
    
  
    width: 55%;
    
}
.thumbnail1{
    max-width: 250px;
    border: 2px solid orangered;
    border-radius: 26px;
    border: 4px solid orangered;
    padding: 4px;
    margin: 0 76px 0 0px;
}
.thumbnail2{
    max-width: 200px;
    margin: 0 0px 0 76px;
}
.para-head{
    font-size: 40px;
   align-content: center;
   margin: 12px 0;
color: orangered;
font-family: ubuntu, sans-serif;
}

.para-body{
    font-size: 15px;
    color: white;  
    font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif; 
}

.thirdSection{
    flex-direction: row-reverse;
    padding-bottom: 40px;
}




/* about section ends here */


/* footer starts from here */
.contact{
    background-color:orangered;
    height: 90px;
    color: white;
    font-family: ubuntu, sans-serif;
    margin: auto;
}
.text-center{
    text-align: center;
   
    justify-content: center;
}

.address{
    display: flex;
}
.email-phone{
    display: flex;
}
.sepreator{
    margin: 0 20px;
}
.info-container{
align-content: center;
   justify-content: center;
   padding-top: 10px;
}

.text-footer{
    text-align: center;
padding: 30px 0;
color: white;
    justify-content: center; 
    font-size: 21px;
}


.burger{
    position: absolute;
    cursor: pointer;
    right: 5%;
    top: 15px;
    display: none;
}

.line{
    width: 33px;
    background-color: white;
    height: 4px;
    margin: 5px 3px;
    border-radius: 2px;
}



@media only screen and (max-width: 1140px){

     
    .logo{
        width: 115px;
        margin: 13px;
    }

    .firstsection{
        
        height: 100vh;
        text-align: center;
        align-items: center;
        justify-content: center;
    }

    .nav-items{
        flex-direction: column;
        align-items: center;
        justify-content: center;
        margin-top: 83px;
    }
    
    
    .navbar{
        height: 100vh;
        flex-direction: column;
        transition: all 0.7s ease-in-out;
    }

    .right-nav{
        width: 60%;
        text-align: center;
    }
    #search{

        width: 100%;

    }


    

    
    .texthalf{
        width: 100%;
    }
    .text-small{
        width: 100%;
    }
    .text-big{
        font-size: 80px;
    }

.imghalf{
    display: none;
}


    .btn-sml {
        padding: 5px 53px;
        font-size: 23px;
        margin-right: 10px;
    }

    .burger{
        display: block;
    }

    .nav-height-resp{
        height: 72px;
    }
    .nav-height-nonresp{
        height: 100vh;
    }
    .visibility-class-nonresp{
        display: none;
    }
    .visibility-class-resp{
        display: none;
    }
    .boxmain{
        flex-direction: column-reverse;
        margin: auto;
        max-width: 100%;
        padding: 0px;
    }


    
    .thumbnail{
        width: 100%;
        display: flex;
        align-items: center;
        justify-content: center;
    }

    .thumbnail1{
        margin: auto;
        width: 90%;
        max-width: 100%;
    }
    
}


@media only screen and (max-width: 900px){
    .secondSection {
        flex-direction: column-reverse;
    }
    .thumbnail{
        width: 50%;
        
    }


}


@media only screen and (max-width: 390px){
    .text-big{
        font-size: 50px;
    }

    .firstsection{
       
        vertical-align: middle;
    }

    .contact{
        height: 119px;
    }
    .info-container{
        font-size: 13px;
    }
    .email-phone{
        flex-direction: column-reverse;
    }

    .thumbnail{
        width: 100%;
        
    }
}
.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

